.slider{
    -webkit-appearance: none;
    background: linear-gradient(90deg, rgb(214,214,214) 60%, rgb(214,214,214) 60%);
  
    outline:none;
    opacity: 0.7;
    -webkit-transition: 0.2s;
    transition: opacity 0.2s;
    border-radius:10px;
    box-shadow: 0px 1px 10px 1px black;

    
}


#color-test::-webkit-slider-thumb{
  -webkit-appearance: none;
 

}

input[type='range']{
    width: 20rem;
}

#question-div{
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items:center;
}

#question {
  display:flex;
  flex-direction: row;
}

#popUp {
  display:flex;
  font-size: small;
}

